.header {
    height: 60px;
    display: flex;
    align-items:  center;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 100;

}

.header__search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;

}
.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_logo {
    width: 100px;
    object-fit: contain;
    margin: 0 25px;
    margin-top: 18px;
}

.header__searchIcon {
    padding: 5px;
    height: 22px;
    background-color: orange;
}

.header__option{
    display: flex;
    color: white;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__nav{
    display:flex;
    justify-content: space-evenly;
}

.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket{
    color: white;
    display: flex;
    align-items: center;
}
.header__basetCount{
    margin-left: 10px;
    margin-right: 10px;
}